<template>
    <div class="box7">
      <listmsg-header></listmsg-header>
      <listmsg-content :msg="contents"></listmsg-content>
      <listmsg-footer></listmsg-footer>
    </div>
</template>

<script>
    import ListmsgHeader from "../components/listmsg/ListmsgHeader";
    import ListmsgContent from "../components/listmsg/ListmsgContent";
    import ListmsgFooter from "../components/listmsg/ListmsgFooter";
    export default {
        name: "listmsg",
      components: {ListmsgFooter, ListmsgContent, ListmsgHeader},
      data(){
        return{
          contents:[]
        }
      },
      mounted() {
        let oog=this.$route.params.id
        console.log(oog)
        fetch("http://localhost:3000/newpro").then(res => {
          res.json().then(data => {
            // console.log(data.contentBanner)
            this.contents = data.list[oog]
          })
        })
      }
    }
</script>

<style >
  @import "../assets/css/public.css";
  @import "../assets/css/listmsg.css";
.box7{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
</style>



