<template>
  <div class="notice">
    <h2>{{noc}}</h2>
    <div class="bannerUp">
      <div class="swiper-container banner-tit">
        <div class="swiper-wrapper" >
        <div class="swiper-slide" :key="index" v-for="(p,index) in noc1" v-if="p">{{p.nocTit}}</div>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
  import Swipercss from "../../../node_modules/swiper/dist/css/swiper.css"
  import Swiper from "swiper"
  import $ from "jquery"
    export default {
        name: "IndexContentNotice",
      components:{ Swipercss,Swiper},
      props:["noc","noc1"],
        mounted() {
        var swiper2 = new Swiper('.banner-tit', {
          direction: 'vertical',
          autoplay:true,
          loop:true,
          autoplayDisableOnInteraction: false,
          observer:true,
          observeParents:true,
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          },
        });
      },
      updated() {
        var swiper2 = new Swiper('.banner-tit', {
          direction: 'vertical',
          autoplay:true,
          loop:true,
          autoplayDisableOnInteraction: false,
          observer:true,
          observeParents:true,
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          },
        });
      }
    }
</script>

<style scoped>
</style>



