<template>
  <div class="box4">
    <newlist-header></newlist-header>
    <newlist-content :cons="contents"></newlist-content>
    <newlist-footer></newlist-footer>
  </div>

</template>

<script>
    import NewlistHeader from "../components/newpro2/newlistHeader";
    import NewlistContent from "../components/newpro2/newlistContent";
    import NewlistFooter from "../components/newpro2/newlistFooter";
    export default {
        name: "newpro2",
      components: {NewlistFooter, NewlistContent, NewlistHeader},
      data(){
        return{
          contents:[]
        }
      },
      mounted() {
        fetch("http://localhost:3000/newpro").then(res => {
          res.json().then(data => {
            console.log(data)
            this.contents = data
          })
        })
      }
    }
</script>

<style >
  @import "../assets/css/public.css";
  @import "../assets/css/newPro2.css";
  .box4{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #fff;
  }
</style>



