<template>
  <div class="special-offer">
    <div class="swiper-container swiper-special">
      <div class="swiper-wrapper">
        <div class="swiper-slide" :key="index" v-for="(s,index) in spec"><a href=""><img :src="s.specImg" alt=""><p>{{s.specP}}</p><span>{{s.specSpan}}</span></a></div>
      </div>
    </div>
  </div>
</template>

<script>
    import Swipercss from "../../../node_modules/swiper/dist/css/swiper.css"
    import Swiper from "swiper"
    import $ from "jquery"
    export default {
        name: "IndexContentSpecial-offer",
      components:{Swipercss,Swiper},
      props:["spec"],
      mounted(){
        var swiper3 = new Swiper('.swiper-special', {
          slidesPerView:3,
          //   autoplay:true,
          loop:true,
          spaceBetween:20,
          freeMode: true,
          observer:true,
          observeParents:true,
          pagination: {
          },
        });
      },
      updated(){
        var swiper3 = new Swiper('.swiper-special', {
          slidesPerView:3,
          //   autoplay:true,
          loop:true,
          spaceBetween:20,
          freeMode: true,
          observer:true,
          observeParents:true,
          pagination: {
          },
        });
      }
    }
</script>

<style scoped>

</style>



