<template>
  <div class="box">
    <index-header></index-header>
    <index-content :con="contents"></index-content>
    <!--:iu="asd-->
    <index-footer></index-footer>
  </div>

</template>

<script>
    import PublicCss from "../assets/css/public.css"
    import IndexHeader from "../components/index/IndexHeader";
    import IndexContent from "../components/index/IndexContent";
    import IndexFooter from "../components/index/IndexFooter";
    export default {
        name: "index",
      components: {IndexFooter, IndexContent, IndexHeader,PublicCss},
      data(){
        return{
          contents:[]
        }
      },
      mounted() {
          fetch("http://localhost:3000/indexcontent").then(res=>{
            res.json().then(data=>{
              // console.log(data.contentBanner)
             this.contents=data
            })
          })
      }
    }

</script>

<style >
  @import "../assets/css/index.css";
  .box{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
</style>



