<template>
  <div class="item">
    <h2>{{item}}</h2>
    <div class="imgBox" :key="index" v-for="(I,index) in item1">
      <img :src="I.itemImg" alt="">
      <h3>{{I.itemTit}}</h3>
      <p>{{I.itemP}}</p>
    </div>
  </div>
</template>

<script>
    export default {
        name: "IndexContentItem",
      props:["item","item1"]
    }
</script>

