import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import Classify from '@/pages/classify'
import Discover from '@/pages/discover'
import Newpro from '@/pages/newpro'
import Newpro2 from '@/pages/newpro2'
import Carts from '@/pages/carts'
import Login from '@/pages/login'
import Listmsg from '@/pages/listmsg'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/classify',
      name: 'classify',
      component: Classify
    },
    {
      path: '/discover',
      name: 'discover',
      component: Discover
    },
    {
      path: '/newpro',
      name: 'newpro',
      component: Newpro
    },
    {
      path: '/newpro2',
      name: 'newpro2',
      component: Newpro2
    },
    {
      path: '/carts',
      name: 'carts',
      component: Carts
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/listmsg/:id',
      name: 'listmsg',
      component: Listmsg
    }
  ]
})



