<template>
  <div>
  <div class="format" :key="index" v-for="(f,index) in fm">
    <div class="title">
      <img :src="f.titimg" alt="">
      <div class="tit-box">
        <h3 class="act-tit">{{f.acttit}}</h3>
        <p>{{f.actp}}</p>
      </div>
    </div>
    <div class="tally">
      <span>{{f.tally}}</span>
    </div>
    <p class="prod-intro">
      {{f.prointro}}
    </p>
    <div class="pro-img">
      <img :src="f.proimg1" >
      <img :src="f.proimg2" alt="">
      <img :src="f.proimg3" alt="">
    </div>
    <div class="slogin">
      <img :src="f.sloginimg" alt="">
      <p>{{f.sloginp}}</p>
    </div>
    <div class="agree">
      <img :src="f.agreeimg" alt="">
      <span>{{f.agreespan}}</span>
    </div>
  </div>
  </div>
</template>

<script>
    export default {
        name: "DiscoverContentFormat",
      props:["fm"]
    }
</script>

<style scoped>

</style>



