<template>
  <section class="indexSection">
     <!--BANNER-->
    <index-content-banner :ban="con.contentBanner"></index-content-banner>
    <!-- NOTICE -->
    <index-content-notice :noc1="con.nocBanner" :noc="con.notice"></index-content-notice>
    <!--:we="iu.pi"-->
    <!-- list -->
<index-content-list :list="con.contentList"></index-content-list>
    <!--setinterval-->
<index-content-time></index-content-time>
    <!-- 左右滑动图 -->
    <index-content-special-offer :spec="con.contentSpecial"></index-content-special-offer>
    <!-- 商品陈列 -->
   <index-content-dshui :dsh="con.contentDshui"></index-content-dshui>
    <!-- 大图 -->
    <index-content-big-img :bimg="con.contentBigimg"></index-content-big-img>
    <!--超值热卖-->
   <index-content-item :item="con.bigTit" :item1="con.itemMsg"></index-content-item>
  </section>
</template>

<script>
    import IndexContentBanner from "./IndexContentBanner";
    import IndexContentNotice from "./IndexContentNotice";
    import IndexContentList from "./IndexContentList";
    import IndexContentTime from "./IndexContentTime";
    import IndexContentSpecialOffer from "./IndexContentSpecial-offer";
    import IndexContentDshui from "./IndexContentDshui";
    import IndexContentBigImg from "./IndexContentBigImg";
    import IndexContentItem from "./IndexContentItem";
    export default {
        name: "IndexContent",
      // props:["iu"],
      props:["con"],
      components: {
        IndexContentItem,
        IndexContentBigImg,
        IndexContentDshui,
        IndexContentSpecialOffer, IndexContentTime, IndexContentList, IndexContentNotice, IndexContentBanner}
    }
</script>

<style >

</style>



