<template>
  <div class="list">
    <router-link :to="r.jumpTo" :key="index" v-for="(r,index) in list"><img :src="r.jumpImg" alt=""><span>{{r.jumpSpan}}</span></router-link>
  </div>
</template>

<script>
    export default {
        name: "IndexContentList",
      props:["list"]
    }
</script>

