<template>
  <div class="box3">
    <newpro-header></newpro-header>
    <newpro-content :con="contents1"></newpro-content>
    <newpro-footer></newpro-footer>
  </div>

</template>

<script>
    import NewproHeader from "../components/newpro/newproHeader";
    import NewproContent from "../components/newpro/newproContent";
    import NewproFooter from "../components/newpro/newproFooter";
    export default {
        name: "newpro",
      components: {NewproFooter, NewproContent, NewproHeader},
      data(){
        return{
          contents1:[]
        }
      },
      mounted() {
        fetch("http://localhost:3000/newpro").then(res => {
          res.json().then(data => {
            // console.log(data.contentBanner)
            this.contents1 = data
          })
        })
      }
    }
</script>

<style >
  @import "../assets/css/public.css";
  @import "../assets/css/newPro.css";
  .box3{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #fff;
  }
</style>



