<template>
    <div class="box2">
      <discover-header></discover-header>
      <discover-content :con="contents"></discover-content>
      <discover-footer></discover-footer>
    </div>
</template>

<script>
    import DiscoverHeader from "../components/discover/DiscoverHeader";
    import DiscoverContent from "../components/discover/DiscoverContent";
    import DiscoverFooter from "../components/discover/DiscoverFooter";
    export default {
        name: "discover",
      components: {DiscoverFooter, DiscoverContent, DiscoverHeader},
      data(){
        return{
          contents:[]
        }
      },
      mounted() {
        fetch("http://localhost:3000/discover").then(res=>{
          res.json().then(data=>{
            // console.log(data.contentBanner)
            this.contents=data
          })
        })
      }
    }
</script>

<style >
  @import "../assets/css/public.css";
  @import "../assets/css/discover.css";
 .box2{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
</style>



