        {

            listtabs:function(index){
              this.con.num = index
              var domObj = document.getElementById("classify-secLeftul");
              // console.log(domObj)
              var lis = domObj.children;
              // console.log(lis)
              if(lis){
                for(var i=0; i<lis.length; i++){
                  lis[i].index = i;
                  lis[i].onclick = li_onclick;
                }
                function li_onclick(){
                  for(var j=0; j<lis.length; j++){
                    lis[j].style.backgroundColor = "#f6f4f5";
                    lis[j].style.borderLeft = "none";
                  }
                  console.log(this)
                  this.style.backgroundColor = "#fff";
                  this.style.borderLeft = ".03rem solid #000";
                  // this.style.fontWeight = "bold"
                }
              }
            }

