<template>
  <div>
    <div class="payment-bar">
      <div class="all-checkbox">
        <input type="checkbox" @change="allChecked" class="check goods-check ins" v-model="items.payful.checked"/>
        {{items.payful.allchek}}
      </div>
      <div class="shop-total" v-if="items.carts">
        <strong>
          {{items.payful.shoptotal}}
          <span id="AllTotal" class="total">{{totalPriceFilter}}</span>
        </strong>
      </div>
      <a href="#" class="settlement">{{items.payful.settlement}}({{totalSum}})</a>
    </div>
  </div>

</template>
<script>
    export default {
        name: "CartsContentBar",
      props:["items","total","allChecked","totalPriceFilter","sunNum","totalSum"],
      // methods:{
      //   ShopAllChecked(){
      //     this.$emit("ShopChecked")
      //   }
      // }
    }
</script>

<style scoped>

</style>



