<template>
  <section class="cartsSection">
    <div class="shop-group">
      <carts-content-shop-list :minus="minus" :plus="plus" :subChecked="subChecked" :items="cartsInfo.carts" ></carts-content-shop-list>
    </div>
    <div>
      <carts-content-bar :totalPriceFilter="totalPriceFilter" :allChecked="allChecked" :total="totalPrice" :totalSum="totalSum" :sunNum="Sum" v-if="cartsInfo.carts" :items="cartsInfo" ></carts-content-bar>
    </div>

  </section>
</template>
<script>
    import CartsContentShopList from "./CartsContentShopList";
    import CartsContentBar from "./CartsContentBar";
    export default {
        name: "CartsContent",
      components: {CartsContentBar, CartsContentShopList},

      data(){
          return {
            cartsInfo: {
              totalPrice:0,
              Sum:0,
              carts: [
                {
                  shopImg: "@/../static/images/p-1.jpg",
                  shopText: "玛莎妮娜多肽水润驻颜修护蚕丝面膜正品补水美白敏感肌、孕妇可用[单片」",
                  shopPrice: "15.0",
                  shopQal: 1,
                  checked: true
                },
                {
                  shopImg: "@/../static/images/p-3.jpg",
                  shopText: "玛莎妮娜多肽水润驻颜修护蚕丝面膜正品补水美白敏感肌、孕妇可用[单片」",
                  shopPrice: "15.0",
                  shopQal: 1,
                  checked: true
                }
              ],
              payful: {
                allchek: "全选",
                shoptotal: "总计￥",
                shopalltotal: "",
                settlement: "去结算",
                checked: true
              }
            }
          }
      },
      methods:{
        _totalPriceFilter(cartsInfo){
          let total = 0
          cartsInfo.carts.forEach((products,pid)=>{
            if(products.checked){
              total +=products.shopPrice*products.shopQal
            }
          })
          this.totalPrice = total;
        },
        /**
         * 全选
         */
        allChecked(){
          let isChecked = this.cartsInfo.payful.checked;
          console.log(isChecked)

          let product = this.cartsInfo.carts;
          for (let i=0;i<product.length;i++) {
            if (isChecked) {
              product[i].checked = true;
            } else {
              product[i].checked = false;
            }
          }
          console.log(product)
        },
        subChecked(){
          let isChecked = this.cartsInfo.payful.checked;
          let product = this.cartsInfo.carts;
          for (let i=0;i<product.length;i++){
            if (product[i].checked){
              isChecked = false;
            }else{
              isChecked = true;
            }
          }
        },
        /**
         * 累加
         * @param pid
         */
        plus(pid){
          this.cartsInfo.carts[pid].shopQal++;
        },
        /**
         * 累减
         * @param pid
         */
        minus(pid){
          if(this.cartsInfo.carts[pid].shopQal<=1){
            this.cartsInfo.carts[pid].shopQal=1
          }else {
            this.cartsInfo.carts[pid].shopQal--
          }
        }
      },
      created(){
        this._totalPriceFilter(this.cartsInfo);
      },
      computed:{
        totalPriceFilter(){
          let total = 0
          let sunNum = 0
          this.cartsInfo.carts.forEach((products,pid)=>{
            if(products.checked){
              total +=products.shopPrice*products.shopQal
              sunNum += products.shopQal
              console.log(sunNum)
            }
          })
          return this.totalPrice = total.toFixed(2);
        },
        totalSum(){
          let total = 0
          let sunNum = 0
          this.cartsInfo.carts.forEach((products,pid)=>{
            if(products.checked){
              total +=products.shopPrice*products.shopQal
              sunNum += products.shopQal
              console.log(sunNum)
            }
          })
          return this.Sum = sunNum;
        }
      }
    }
</script>

<style >

</style>



