<template>
  <div>
    <div class="products" :key="index" v-for="(f,index) in prods">
      <div class="img-list">
        <router-link :to="'/listmsg/'+index"><img :src="f.listImg" alt=""></router-link>
      </div>
      <div class="p-md-1">
        <p class="pro-msg">{{f.listMsg}}</p>
        <p class="pro-price">{{f.listMoney}}<span>{{f.listPeop}}</span></p>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "NewlistContentProduct",
        props:["prods"]
    }
</script>

<style scoped>

</style>



