<template>
  <div class="banner">
    <div class="swiper-container banLeft">
      <div class="swiper-wrapper">
          <!--<img :src="er.pic" >-->
          <div class="swiper-slide" :key="index" v-for="(b,index) in ban" v-if="b"><img :src="b.banImg"></div>
      </div>
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
  import Swipercss from "../../../node_modules/swiper/dist/css/swiper.css";
  import $ from "jquery"
  import Swiper from "swiper"
    export default {
        name: "IndexContentBanner",
      components: {Swipercss,Swiper},
      props:["ban"],
      mounted(){
        var swiper1 = new Swiper('.banLeft', {
          spaceBetween: 30,
          centeredSlides: true,
          loop:true,
          autoplay: {
            delay: 2500,
            disableOnInteraction: true,
            observer:true,
            observeParents:true
          },
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          }
        });
      },
      updated(){
        var swiper1 = new Swiper('.banLeft', {
          spaceBetween: 30,
          centeredSlides: true,
          loop:true,
          autoplay: {
            delay: 2500,
            disableOnInteraction: true,
            observer:true,
            observeParents:true
          },
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          }
        });
      }
    }
</script>

<style scoped>

</style>



