<template>
  <div class="box1">
    <classify-header></classify-header>
    <classify-content :con="contents"></classify-content>
    <classify-footer></classify-footer>
  </div>
</template>

<script>
    import ClassifyHeader from "../components/classify/ClassifyHeader";
    import ClassifyContent from "../components/classify/ClassifyContent";
    import ClassifyFooter from "../components/classify/ClassifyFooter";
    export default {
        name: "classify",
      components: {ClassifyFooter, ClassifyContent, ClassifyHeader},
      data(){
        return{
          contents:[]
        }
      },
      mounted() {
        fetch("http://localhost:3000/classify").then(res=>{
          res.json().then(data=>{
            // console.log(data.contentBanner)
            this.contents=data
          })
        })
      }
    }
</script>

<style >
  @import "../assets/css/public.css";
  @import "../assets/css/classify.css";
  .box1{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
</style>



