<template>
  <div>
    <div class="shop-info" :key="pid" v-for="(c,pid) in items" v-model="c.checked">
      <input type="checkbox" @change="subChecked"
             class="check goods-check goodsCheck ins" v-model="c.checked"/>
      <div class="shop-info-img">
        <a href="">
          <img :src="c.shopImg" />
        </a>
      </div>
      <div class="shop-info-text">
        <h4>{{c.shopText}}
        </h4>
        <div class="shop-price">
          <div class="shop-pices">
            ￥<b class="price"> {{c.shopPrice}}</b>
          </div>
          <div class="shop-arithmetic">
            <a class="minus" @click="minus(pid)">-</a>
            <span class="num">{{c.shopQal}}</span>
            <a class="plus" @click="plus(pid)">+</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "CartsContentShopList",
      props:["items","sid","pid","subChecked","plus","minus"]
    }
</script>

<style scoped>

</style>



